.title {
    margin-top: 3rem;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.subtitle {
    margin-top: 0.5rem;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
      text-align: center;
}
.quote {
    margin-top: 3rem;
    text-align: center;
    font-size: 25px;
    color: #111;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
      padding-bottom: 1rem;
      margin-right: 16rem;
      margin-left: 16rem;
}

.image {
    border-radius: 10px;
    box-shadow: 0px 10px 50px #555;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3rem;
}

.authorbox{
    width: 1000px;
    background-color: #1a83ff;
    border-radius: 10px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5rem;
    box-shadow: 0px 1px 50px #555;
}

.authorText {
    color:#ffffff;
    padding: 10px, 10px, 10px, 10px;
}

