.goLeft{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 10%;
    height: 80%;
    background: None;
    border: none;
    outline: none;
    z-index: 0;
}

.goLeft:hover {
    background: rgba(0, 0, 0, 0.356);
    cursor: pointer;
    color: whitesmoke;
}

.goRight{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 10%;
    height: 80%;
    background: None;
    border: none;
    outline: none;
    z-index: 0;
}

.goRight:hover {
    background: rgba(0, 0, 0, 0.356);
    cursor: pointer;
    color: whitesmoke;
}
